/* eslint-disable react/prop-types */
import React from 'react';

import { createUseStyles } from 'react-jss';
import Link from './Link';

import { useSoundController } from './SoundController/SoundController';

const useStyles = createUseStyles({
  '@keyframes appear': {
    '0%': {
      opacity: 0,
    },
    '100%': {
      opacity: 1,
    },
  },
  '@keyframes wormhole': {
    '0%': {
      transform: 'scale(1.8, 1.8) rotateZ(359deg)',
      opacity: 0,
    },
    '100%': {
      transform: 'scale(1, 1) rotateZ(0deg)',
      opacity: 1,
    },
  },
  root: {
    position: 'relative',
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    // overflow: 'hidden',
    maxWidth: '100%',
    maxHeight: '100%',
  },
  logo: {
    animationName: '$appear',
    animationDirection: 'reverse',
    animationDuration: '500ms',
    animationDelay: '4500ms',
    animationFillMode: 'both',
    zIndex: 1,
    position: 'relative',
    // maxWidth: '80vw',
    // maxHeight: '80vh',
    margin: 'auto',
  },
  wormhole: () => ({
    maxWidth: 'none',
    transformOrigin: 'center',
    animationName: '$wormhole',
    animationDuration: '5500ms',
    animationDelay: '500ms',
    animationFillMode: 'both',
    zIndex: 2,
    position: 'fixed',
    left: '50%',
    top: '50%',
    margin: {
      top: '-50%',
      left: '-50%',
    },
  }),
  text: {
    textAlign: 'center',
    margin: 'auto',
    width: '80vw',
    maxWidth: 750,
    animationName: '$appear, $appear',
    animationDuration: '500ms, 500ms',
    animationDelay: '6500ms, 12000ms',
    animationFillMode: 'backwards, forwards',
    animationDirection: 'normal, reverse',
    zIndex: 3,
    position: 'fixed',
    fontFamily: 'KRSNA-DREAMER',
    fontSize: '2rem',
    lineHeight: '2.2rem',
    textTransform: 'lowercase',
  },
  playButtonsWrapper: {
    display: 'flex',
    animationName: '$appear',
    animationDuration: '500ms',
    animationDelay: '12000ms',
    animationFillMode: 'both',
    zIndex: 4,
    position: 'fixed',
  },
  play: {
    margin: '0 10px',
  },
}, { name: 'MessageScreen' });

const MessageScreen = (props) => {
  const {
    srcSets,
    description,
  } = props;

  const classes = useStyles(props);

  const {
    setMuted,
  } = useSoundController() || {};

  return (
    <div className={classes.root}>
      <img
        className={classes.logo}
        srcSet={srcSets?.MKS_LOGO_GR_1980?.srcSet}
        alt="METAKRONISMS"
      />
      <img
        className={classes.wormhole}
        srcSet={srcSets?.MKS_Wormhole_01?.srcSet}
        alt="wormhole"
      />
      <div
        className={classes.text}
      >
        {description}
      </div>

      <div className={classes.playButtonsWrapper}>
        <Link
          to="episodes/s0/e0/"
          onClick={() => setMuted(false)}
          button
          className={[
            classes.play,
            classes.buttonInsaneBig,
          ].join(' ')}
        >
          Play with sound
        </Link>

        <Link
          to="episodes/s0/e0/"
          onClick={() => setMuted(true)}
          button
          className={[
            classes.play,
            classes.buttonInsaneBig,
          ].join(' ')}
        >
          Play withouth sound
        </Link>
      </div>
    </div>
  );
};

export default MessageScreen;
